import React, { useEffect, useState } from "react";
import { useLocation, useHistory } from "react-router-dom";
import { postImg } from "./actions";
import Loading from "./Loading";

import { CircularProgressbar } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
import AutoSizer from "react-virtualized-auto-sizer";

const getEmotions = (person) =>
  Object.entries(person.faceAttributes.emotion)
    .map((e) => [e[0], Math.round(e[1] * 100, 0)])
    .filter((e) => e[1] !== 0)
    .sort((a, b) => b[1] - a[1]);

function Emotion() {
  const location = useLocation();
  const b64_img = (location.state || {}).b64_img;
  const history = useHistory();
  const [isLoading, setIsLoading] = useState(false);
  const [data, setData] = useState([]);
  const [error, setError] = useState("");

  useEffect(() => {
    if (!b64_img) history.push("/face-capture");
    else {
      setIsLoading(true);
      setError("");
      postImg(b64_img)
        .then((data) => {
          setIsLoading(false);
          setData(data);
        })
        .catch((e) => {
          setIsLoading(false);
          setError(e.message);
        });
    }
  }, [b64_img, history]);

  if (error)
    return (
      <div
        style={{
          flex: 1,
          display: "flex",
          flexDirection: "column",
        }}
      >
        <h1>{error}</h1>
      </div>
    );

  return (
    <div
      style={{
        flex: 1,
        display: "flex",
        flexDirection: "column",
      }}
    >
      {isLoading ? (
        <Loading />
      ) : data.length > 0 ? (
        <div
          style={{
            display: "flex",
            width: "100%",
            scrollX: "auto",
          }}
        >
          {data.map((person, index) => {
            const emotions = getEmotions(person);
            const age = person.faceAttributes.age;
            return (
              <div key={index} style={classes.emotionBar}>
                <div
                  style={{ display: "flex", flex: 1, justifyContent: "center" }}
                  className="values"
                >
                  <Value value={age} text={`${age} years`} key={0} />
                  {emotions.map((e, index) => (
                    <Value value={e[1]} text={e[0]} key={index + 1} />
                  ))}
                </div>
              </div>
            );
          })}
        </div>
      ) : (
        "No data available."
      )}
      <div
        style={{
          display: "flex",
          flex: 1,
        }}
      >
        <AutoSizer>
          {({ height, width }) => (
            <img
              src={b64_img}
              alt="selfi"
              style={{ width: width, height: height, objectFit: "contain" }}
            />
          )}
        </AutoSizer>
      </div>
    </div>
  );
}

function Value({ value, text }) {
  return (
    <div
      style={{
        padding: "20px 20px 20px 20px",
        width: "125px",
      }}
    >
      <CircularProgressbar value={value} text={text} />
    </div>
  );
}

const classes = {
  emotionBar: {
    flex: 1,
    display: "flex",
    overflowX: "scroll",
  },
};

export default Emotion;
